
import { defineComponent, ref } from 'vue'

import Navi from "../components/Navi.vue"

import {Dialog, Toast} from 'vant'

import {useRouter} from 'vue-router'

import axios from 'axios'

export default defineComponent({
  components: {
    Navi
  },

  setup () {

    let router: any = useRouter();
    
    let isOpen: any = ref<boolean>(false);

    let passwords = ref<any>({
        old: {
          password: "",
          isPassword: true,
          title: "旧密码",
        },
        new: {
          password: "",
          isPassword: true,
          title: "新密码",
        },
      });

    //查看密码
    let viewPassword: (item: any) => void = (item: any): void => {
      item.isPassword = !item.isPassword;
    }

    //退出登录
    let logout: () => void = (): void => {
      Dialog
        .confirm({
          title: "退出登录",
          message: "是否确认退出登录！",
          confirmButtonColor: "#0C34BA",
        })
        .then((): void => {

          localStorage.removeItem("token");

          setTimeout((): void => {
            router.push({ name: "Login" });
          }, 1000);
        });
    }

    //修改密码
    let openPasswordPopup: () => void = (): void => {
      isOpen.value = true;
    }

    //确认修改密码
    let confirmPassword: () => void = (): void => {

      axios({
        method: "POST",
        url: "/updatePassword",
        data: {
          oldPassword: passwords.value.old.password,
          password: passwords.value.new.password
        }
      })
        .then((result: any): void => {

          Toast(result.data.msg);
          
          if (result.data.code == "E001") {
            localStorage.removeItem("token");
            setTimeout((): void => {
              router.push({ name: "Login" });
            }, 1000);
          }
        })
        .catch((err: any): void => {
          
        });
    }

    //注销账号
    let destroyAccount: () => void = (): void => {
      Dialog
        .confirm({
          title: "注销账号",
          message: "是否确认注销号，一旦确认无法恢复！",
          confirmButtonColor: "#0C34BA",
        })
        .then((): any => {

          axios({
            method: "POST",
            url: "/destroyAccount"
          })
            .then((result: any): void => {
              
              Toast(result.data.msg);
              if (result.data.code == "G001") {
                localStorage.removeItem("token");
                setTimeout(() => {
                  router.push({ name: "Login" });
                }, 1000)
                
              }
            })
            .catch((err: any): void => {
              
            });
        })
        .catch((err): void => {
          
        });
    }

    return {
      isOpen,
      passwords,
      viewPassword,
      logout,
      openPasswordPopup,
      confirmPassword,
      destroyAccount
    }
  }
})
